<template>
  <el-container class="common-layout" ref="divDom">
    <el-aside>
      <el-scrollbar>
        <div class="website-left">
          <div class="website-left-box" v-for="(item, index) in arrey" key="index">
            <div :class="{ active: item.key == current }" @click="liClick(item.key)">
              {{ item.title }}</div>
          </div>
        </div>
      </el-scrollbar>
    </el-aside>
    <el-main>
      <el-scrollbar ref="scrollbarRef">
        <div style="height: 40px;"></div>
        <div class="website-right-box">
          <div class="website-right-title" v-for="(item, index) in arrey" :key="index" :id="item.key">
            <h2>{{ item.title }}</h2>
            <div class="box">
              <div class="webBox" v-for="(res, index) in item.childers">
                <template v-if="res.jianjie">
                  <div class="jianjie">{{ res.jianjie }}</div>
                </template>
                <a :href="res.url" target="_blank" :key="index">
                  <div class="webUrl">
                    <img :src="res.imgUrl ? res.imgUrl : 'https://img.bidianer.com/image/icon/empty.svg'">
                    <div>{{ res.webName }}</div>
                  </div>
                </a>
                <div class="bianji" v-if="isShowEdit">
                  <el-dropdown trigger="click">
                    <span class="el-dropdown-link">
                      <el-icon class="on-inline">
                        <More />
                      </el-icon>
                    </span>
                    <template #dropdown>
                      <el-dropdown-menu>
                        <el-dropdown-item @click="edit(item, res, index)">
                          <el-icon>
                            <Edit />
                          </el-icon>编辑</el-dropdown-item>
                        <el-dropdown-item @click="del(item, res, index)"><el-icon>
                            <Delete />
                          </el-icon>删除</el-dropdown-item>
                      </el-dropdown-menu>
                    </template>
                  </el-dropdown>
                </div>
              </div>
            </div>
          </div>
        </div>
      </el-scrollbar>

    </el-main>
  </el-container>
  <el-dialog v-model="dialogVisible" title="修改(开发中)" width="30%">
    <el-form label-width="100px" :model="dailogData" style="max-width: 460px">
      <el-form-item label="分类">
        <el-select v-model="dailogData.sort" placeholder="请选择一个分类">
          <el-option v-for="(item, index) in dailogData.selectData" :key="index" :label="item" :value="item" />
        </el-select>
      </el-form-item>
      <el-form-item label="网站名称">
        <el-input v-model="dailogData.title" />
      </el-form-item>
      <el-form-item label="网页地址">
        <el-input v-model="dailogData.url" />
      </el-form-item>
      <el-form-item label="网页图标地址">
        <el-input v-model="dailogData.imgUrl" />
      </el-form-item>
    </el-form>
    <template #footer>
      <span class="dialog-footer">
        <el-button @click="dialogVisible = false">取消</el-button>
        <el-button type="primary" @click="confirm">
          确定
        </el-button>
      </span>
    </template>
  </el-dialog>
</template>
<script setup>
import { inject, provide, ref, onMounted, computed, reactive, nextTick, watchEffect } from 'vue';
import { useRouter } from 'vue-router'
import { useStore } from 'vuex';
import { ElMessageBox, ElMessage } from 'element-plus'

const store = useStore()
const arrey = reactive(computed(() => store.state.webArry)).value
// 获取dom宽度
const divDom = ref(null);
// 是否显示修改按钮
const isShowEdit = ref(false);
// console.log(arrey);
const scrollbarRef = ref();
// console.log(arrey)
// console.log(useRouter());
// const go = (key, e) => {
//   console.log(e)
//   document.getElementById(key).scrollIntoView({ behavior: 'smooth' });
// }

const current = ref("freeMovies");
const flag = ref(false);
const dialogVisible = ref(false)
const liClick = (key) => {
  flag.value = true;
  current.value = key;

  /* 
  获取到key 然后渠道对应的元素 进行滚动
   */
  // 获取父级容器
  // let content = document.querySelector(".website-right-box");
  let target = document.querySelector(`#${key}`);
  // document.getElementById(key).scrollIntoView({ behavior: 'smooth', inline: 'start' });
  scrollbarRef.value.scrollTo({
    top: target.offsetTop - 40,
    behavior: "smooth",
  })

  // content.scrollTo({
  //   // 距离顶部导航栏的距离
  //   top: target.offsetTop - 60,
  //   behavior: "smooth",
  // });


};

// 所有dom元素的id
const showKeys = reactive([]);
// -----交叉观察器

let io = new IntersectionObserver((entrys) => {
  // console.log("进入视口--", entrys[0].target.id);
  let firstInsetId = entrys[0].target.id;
  let isInter = entrys[0].isIntersecting;
  // console.log(11111, isInter);

  if (isInter) {
    let isFind = showKeys.findIndex((item) => item == firstInsetId);
    if (isFind == -1) {
      showKeys.push(firstInsetId);
    }
  } else {
    let index = showKeys.findIndex((item) => item == firstInsetId);
    if (index !== -1) {
      showKeys.splice(index, 1);
    }
  }

  if (flag.value) return; //----放开是另一种效果
  let result = "";
  for (const item of arrey) {
    if (showKeys.findIndex((key) => key === item.key) !== -1) {
      result = item.key;
      break;
    }
  }
  current.value = result;
}, {
  threshold: [0, 0.25, 0.5, 1]
}
);

const startObser = () => {
  nextTick(() => {
    for (const item of arrey) {
      let el = document.querySelector(`#${item.key}`);
      showKeys.push(item.key);
      io.observe(el);
    }
  })
};

const windowWidth = ref(window.innerWidth)
watchEffect(() => {
  if (windowWidth.value < 500) {
    isShowEdit.value = false
  } else {
    isShowEdit.value = true
  }
})
window.addEventListener('resize', () => {
  windowWidth.value = window.innerWidth
});


onMounted(() => {
  let content = document.querySelector(".website-right-box");
  content.addEventListener("mousewheel", () => {
    flag.value = false;
  });
  startObser();
});
// 弹框数据
const dailogData = reactive({
  sort: '',
  title: '',
  url: '',
  imgUrl: ''
})
// 修改
const edit = function (item, childer, index) {
  dailogData.sort = item.title;
  dailogData.selectData = arrey.map(item => item.title)
  dialogVisible.value = true;
  dailogData.title = childer.webName
  dailogData.url = childer.url
  dailogData.imgUrl = childer.imgUrl
}
const del = function (item, childer, index) {
  ElMessage({
    message: '该功能正在开发中,过几天再来看看吧',
    type: 'warning',
  })
}
const confirm = function () {
  ElMessage({
    message: '该功能正在开发中,过几天再来看看吧',
    type: 'warning',
  })
  dialogVisible.value = false;
}

</script>
